import { Component,OnInit } from '@angular/core';
import {FormGroup,FormBuilder,Validators} from '@angular/forms'
import {RoundtaskService} from '../roundtask.service'
import * as globSymb from '../global'

@Component({
  selector: 'app-roundtask',
  templateUrl: './roundtask.component.html',
  styleUrls: ['./roundtask.component.css']
})
export class RoundtaskComponent implements OnInit {

  errorMessage:String="";

  userId:any=0;

  roundTask:Object[]=[];

  lastRoundTask:Object[]=[];
  showLastRound:boolean =false;

  installSts:any=0;

  constructor(private _roundtaskService:RoundtaskService) { }

  ngOnInit() 
  {

    this._roundtaskService.getRoundTask()
        .then(roundTask =>{this.roundTask = roundTask},
        error => this.errorMessage=<any>error);
    
  }

  updateRound(userId:any)
  {

    //console.log("user id",userId);

    this._roundtaskService.updateRoundTask(userId)
        .then(roundTask => this.roundTask = roundTask,
        error => this.errorMessage=<any>error);

  }

  getLastRound()
  {
    if(!this.showLastRound)
    {    

    //console.log("user id",userId);
    this._roundtaskService.getLastRoundTask()
        .then(lastRoundTask => 
        {
          this.showLastRound=true;
          this.lastRoundTask = lastRoundTask;
          
        },
        error => this.errorMessage=<any>error);
    }else
    {
      this.showLastRound=false;
    }

  }

  install()
  {
    this._roundtaskService.install()
        .then(installSts => this.installSts = installSts,
        error => this.errorMessage=<any>error);
  }

}



