import { Injectable } from '@angular/core';
import { Http, Headers, Response, RequestOptions } from '@angular/http';
import 'rxjs/add/operator/toPromise';
import * as globalSymbol from "./global"

@Injectable()
export class RoundtaskService {

  isAuthenticated:boolean =false;

  constructor(private http: Http) {
  }

  //API to update and get the round tasks
  updateRoundTask(uerId:number): Promise<Object[]> 
  {
    
    var userIdQry='userId=' + uerId;

    return this.http.get(`${globalSymbol.baseURL}/updateTaskDone` + 
    globalSymbol.qryParamHead + userIdQry, { headers: this.getHeaders() })
      .toPromise()
      .then(this.extractData)
      .catch(this.handleError);
  }

  // API to get the round tasks
  getRoundTask(): Promise<Object[]> {
    return this.http.get(`${globalSymbol.baseURL}/gettskNotifyList`, { headers: this.getHeaders() })
      .toPromise()
      .then(this.extractData)
      .catch(this.handleError);
  }

  // API to get the round tasks
  getLastRoundTask(): Promise<Object[]> {
    return this.http.get(`${globalSymbol.baseURL}/getLastTaskUpdTran`, { headers: this.getHeaders() })
      .toPromise()
      .then(this.extractData)
      .catch(this.handleError);
  }

  install(): Promise<Object> {
    return this.http.get(`${globalSymbol.baseURL}/install`, { headers: this.getHeaders() })
      .toPromise()
      .then(this.extractData)
      .catch(this.handleError);
  }


// API used to handle the error
  private handleError(error: any): Promise<any> {
    console.error('An error occurred', error); // for demo purposes only
    return Promise.reject(error.message || error);
  }

   // API to get the headers
  private getHeaders() {
    let headers = new Headers();
    headers.append('Accept', 'application/json');
    return headers;
  }

  // API used to extract data to json object array
  private extractData(res: Response) {
    console.log("res", res);
    let resbody = res.json() as Object[];
    console.log(resbody, "data");
    return resbody;
  }
  
  

}



